export default {
	data() {
		return {
			birthday:"2019.12.18",
			globalUser:{}
		};
	},
	onLoad() {
		var _this = this;
		_this.globalUser = _this.getGlobalUser("globalUser");
		_this.birthday = _this.globalUser.birthday;
	},
	methods: {
		//选择日期
		dateChange(e){
			this.birthday = e.detail.value;
			console.log(this.birthday);
		},
		formSubmitBirthday(){
			var _this = this;
			var birthday = _this.birthday;
			uni.request({
				url:_this.serverUrl+'/user/xxxxxx',
				method:"POST",
				data:{
					"birthday":birthday,
					"userId":_this.globalUser.id
				},
				header:{
					"headerUserId": _this.globalUser.id,
					"headerUserToken": _this.globalUser.userUniqueToken
				},
				success(res) {
					console.log(res);
					var resData = res.data;
					if(resData.status == 200){
						//获取最新用户数据
						var userInfo = resData.data;
						uni.setStorageSync("globalUser",userInfo);
						uni.navigateBack({
							delta:1
						})
					}else if(resData.status == 502 || resData.status == 500){
						uni.showToast({
							title:res.data.msg,
							image:"",
							duration:20000
						})
					}
				}
			})
		}
		
	}
}
